<template>  
  <Map v-if="loaded" :mode="'game'" :geojson="geojson" :mapOptions="options" />
</template>
<script>
import Map from '../components/Map'

export default {
  data () {
    return {
      geojson: [],
      options: {
        zoom: 2,
        center: [0, 0],
        maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
      },
      loaded: false
    }
  },
  created () {
    this.getContinent()
  },
  methods: {
    getContinent () {
      let xhr = new XMLHttpRequest()
      xhr.open('GET', `../json/full.geo.json`, true)
      xhr.onload = () => {
        if (xhr.status === 200) {
          this.geojson = [...JSON.parse(xhr.responseText).features]
          this.loaded = true
        }
        else {
          this.loaded = true
        }
      }
      xhr.send()
    }
  },
  components: {
    Map
  }
}
</script>


