<template>  
  <Map v-if="loaded" :mode="mode" :geojson="geojson" :mapOptions="options" />
</template>
<script>
import Map from './Map'

export default {
  data () {
    return {
      geojson: [],
      options: {},
      loaded: false
    }
  },
  props: {
    mode: {
      type: String
    }
  },
  created () {
    this.getContinent()
    this.getOptions()
  },
  computed: {
    name () {
      return this.$route.params.name
    }
  },
  methods: {
    getContinent () {
      let xhr = new XMLHttpRequest()
      xhr.open('GET', `../json/${this.name}.geo.json`, true)
      xhr.onload = () => {
        if (xhr.status === 200) {
          this.geojson = [...JSON.parse(xhr.responseText).features]
          this.loaded = true
        }
        else {
          this.loaded = true
        }
      }
      xhr.send()
    },
    getOptions () {
      switch (this.name) {
        case 'full':
          this.options = {...{
            zoom: 2,
            center: [0, 0],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
        case 'europe':
          this.options = {...{
            zoom: 3,
            center: [60, 60],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
        case 'asia':
          this.options = {...{
            zoom: 3,
            center: [30, 120],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
        case 'africa':
          this.options = {...{
            zoom: 3,
            center: [0, 30],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
        case 'na':
          this.options = {...{
            zoom: 3,
            center: [50, -150],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
        case 'sa':
          this.options = {...{
            zoom: 3,
            center: [-60, -120],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
        case 'oceania':
          this.options = {...{
            zoom: 4,
            center: [-30, 150],
            maxBounds: [[90, -180], [-58.995311187950925, 223.2421875]]
          }}
          break
      }
    }
  },
  components: {
    Map
  }
}
</script>


